import mInput from '../../components/m-input.vue'
 export default {
    components: {
        mInput
    },
    data() {
		return{
			shopObj:{},
			oldPassword:"",
			password:"",
			passwordok:"",
		}
	},
	methods:{
		sava(){
			let that=this;
			if(that.passwordok!=that.password){
				uni.showToast({
					title:"两次输入的密码不一致",
					icon:"none"
				})
				return
			}
			that.$ajax({
				url:"/system/changePassword",
				data:{
					type:0,
					managerId:that.shopObj.managerId,
					newPassword:that.password,
					originalPassword:that.oldPassword
				},success(d){
					uni.showToast({
						title:"修改成功",
						icon:"none"
					})
					that.code="";
					that.password="";
					that.passwordok="";
					that.oldPassword="";
					setTimeout(function() {
						uni.navigateBack({
							delta:1
						})
					}, 1500);
				}
			})
		}
	},
	onLoad(){
		var that=this;
		uni.getStorage({
			key:"shopObj",
			success(res){
				that.shopObj=JSON.parse(res.data);
			}
		})
	}
}
